import * as React from 'react';
import loadable from '@loadable/component';
const Arroz = loadable(() => import('../arroz-con-webo'));

export default function IngredientSheet({ingredients, open, handleOpen}) {
    return (
        <Arroz.SideSheet open={open}>
            <Arroz.Subheading>Ingredients</Arroz.Subheading>
            <Arroz.TonalButton className="recipe-ingredients-pin" pill onClick={() => handleOpen(false)}>Unpin</Arroz.TonalButton>
            <Arroz.Body>
                {ingredients?.map(ingredient => {
                return (
                    <Arroz.Checkbox className="recipe-ingredient" label={ingredient.measure + ' ' + ingredient.unit + ' ' + ingredient.name} />
                );
                })}
            </Arroz.Body>
        </Arroz.SideSheet>
    );
}