import * as React from 'react';
import loadable from '@loadable/component';
const Arroz = loadable(() => import('../arroz-con-webo'));

export default function RecipeDirections({steps}) {
    return (
        <Arroz.Body>
            <Arroz.Subheading>Directions</Arroz.Subheading>
            <ol>
            {steps?.map(step => {
                return (<li key={step}>
                {step}
                </li>);
            })}
            </ol>
        </Arroz.Body>
    );
}