import * as React from 'react';
import loadable from '@loadable/component';
const Arroz = loadable(() => import('../arroz-con-webo'));

export default function TagList({tags}) {
    return (
        tags?.map(tag => {
            return (
              <Arroz.Label role="secondary" pill>{tag.name + ': ' + tag.desc}</Arroz.Label>
            );
        })
    );
}