import * as React from 'react';
import loadable from '@loadable/component';
const Arroz = loadable(() => import('../arroz-con-webo'));

export default function IngredientCard({ingredients, open, handleOpen}) {
    return (
        <Arroz.Collapse open={open} direction='vertical'>
            <Arroz.OutlinedCard className="recipe-ingredients" role="primary" containerType="container" rounded>
                <Arroz.Subheading>Ingredients</Arroz.Subheading>
                <Arroz.FilledButton className="recipe-ingredients-pin" pill onClick={() => handleOpen(true)}>Pin</Arroz.FilledButton>
                <Arroz.Body>
                    {ingredients?.map(ingredient => {
                        return (
                        <Arroz.Checkbox className="recipe-ingredient" label={ingredient.measure + ' ' + ingredient.unit + ' ' + ingredient.name} />
                        );
                    })}
                </Arroz.Body>
            </Arroz.OutlinedCard>
        </Arroz.Collapse>
    );
}

