import * as React from 'react';
import loadable from '@loadable/component';
const Arroz = loadable(() => import('../arroz-con-webo'));

export default function RecipeInformation({image, name, culture, description}) {
    return (
        <Arroz.FilledCard className="recipe-metadata" role="secondary" containerType="container" rounded >
            <Arroz.Image className="recipe-image" source={image} alternate={""} />
            <div className="recipe-info">
                <Arroz.Headline>{name}</Arroz.Headline>
                <Arroz.Label>{culture}</Arroz.Label>
                <Arroz.Subheading>Description</Arroz.Subheading>
                <Arroz.Body>
                    {description}
                </Arroz.Body>
            </div>
        </Arroz.FilledCard>
    );
}